import React, { Component } from "react";
import Map from "../Map";
import VR from "../VR";
import {
  DropdownContainer,
  FloorNav,
  TourContainer,
  MapContainer,
  MapButton
} from "./style";

export default class Tour extends Component {
  state = {
    currentScene: { wing: "A", floor: "1" },
    mapOpened: false
  };

  handleWingChange = e => {
    const { wing, floor } = e.target.dataset;
    this.setState(prevState => ({
      ...prevState,
      currentScene: { wing, floor }
    }));
  };

  render() {
    console.log(this.state);
    return (
      <TourContainer>
        <FloorNav>
          <p className="text-center">Select a wing and a floor:</p>
          <DropdownContainer className="wings">
            <div className="dropdown">
              <div
                className="dropdown-toggle"
                id="dropdownMenuButton"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                A Wing
              </div>
              <div
                className="dropdown-menu"
                aria-labelledby="dropdownMenuButton"
              >
                <a
                  className="dropdown-item"
                  data-wing="A"
                  data-floor="1"
                  onClick={this.handleWingChange}
                >
                  1st Floor
                </a>
                <a
                  className="dropdown-item"
                  data-wing="A"
                  data-floor="2"
                  onClick={this.handleWingChange}
                >
                  2st Floor
                </a>
              </div>
            </div>
            <div className="dropdown">
              <div
                className="dropdown-toggle"
                id="dropdownMenuButton"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                B Wing
              </div>
              <div
                className="dropdown-menu"
                aria-labelledby="dropdownMenuButton"
              >
                <a
                  className="dropdown-item"
                  data-wing="B"
                  data-floor="1"
                  onClick={this.handleWingChange}
                >
                  1st Floor
                </a>
                <a
                  className="dropdown-item"
                  data-wing="B"
                  data-floor="2"
                  onClick={this.handleWingChange}
                >
                  2st Floor
                </a>
              </div>
            </div>
            <div className="dropdown">
              <div
                className="dropdown-toggle"
                id="dropdownMenuButton"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                C Wing
              </div>
              <div
                className="dropdown-menu"
                aria-labelledby="dropdownMenuButton"
              >
                <a
                  className="dropdown-item"
                  data-wing="C"
                  data-floor="1"
                  onClick={this.handleWingChange}
                >
                  1st Floor
                </a>
                <a
                  className="dropdown-item"
                  data-wing="C"
                  data-floor="2"
                  onClick={this.handleWingChange}
                >
                  2st Floor
                </a>
                <a
                  className="dropdown-item"
                  data-wing="C"
                  data-floor="3"
                  onClick={this.handleWingChange}
                >
                  3st Floor
                </a>
              </div>
            </div>
            <div className="dropdown">
              <div
                className="dropdown-toggle"
                id="dropdownMenuButton"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                D Wing
              </div>
              <div
                className="dropdown-menu"
                aria-labelledby="dropdownMenuButton"
              >
                <a
                  className="dropdown-item"
                  data-wing="D"
                  data-floor="1"
                  onClick={this.handleWingChange}
                >
                  1st Floor
                </a>
                <a
                  className="dropdown-item"
                  data-wing="D"
                  data-floor="2"
                  onClick={this.handleWingChange}
                >
                  2st Floor
                </a>
                <a
                  className="dropdown-item"
                  data-wing="D"
                  data-floor="3"
                  onClick={this.handleWingChange}
                >
                  3st Floor
                </a>
              </div>
            </div>
          </DropdownContainer>
        </FloorNav>

        {/* Map Button */}
        <MapButton
          onClick={() => {
            this.setState({ mapOpened: !this.state.mapOpened });
          }}
        >
          <i class="fas fa-map-marked-alt" />
        </MapButton>
        {/* Map */}
        <MapContainer opened={this.mapOpened}>
          <Map scene={this.state.currentScene} />
        </MapContainer>

        {/* React 360 iframe   */}
        <VR />
      </TourContainer>
    );
  }
}
